var currentPage = 1;
var pageSize = 10; //每页的最大记录数
var pageCount = 0; //总页数
var totalItem = 0; //总记录数
export default {
	data() {
		return {
			carouselList: [{
					src: "/static/temp/banner3.jpg",
					background: "rgb(203, 87, 60)",
				},
				{
					src: "/static/temp/banner2.jpg",
					background: "rgb(205, 215, 218)",
				},
				{
					src: "/static/temp/banner4.jpg",
					background: "rgb(183, 73, 69)",
				}
			],
			productList: [],
			loadingData: "加载更多"
		}
	},
	methods: {

		navToDetailPage(data) {
			var id = data.id;
			uni.navigateTo({
				url: "../product/product?id=" + id
			})
		},

		//加载更多 在productList原有的基础上继续加载数据 所以是不能清空的
		more(flag) {
			this.loadingData = "加载中"
			//如果已经加载完了所有的数据那么就不要再允许发起请求了 节省服务器的压力
			if (currentPage >= pageCount) {
				this.loadingData = "已经到底了"
				uni.hideLoading();
				return;
			}
			//当前页加1
			currentPage++;
			this.getProductList(flag)
		},

		//数据请求的总接口
		getProductList(flag) {
			//flag=1 表示 下拉的标志位  flag=0 的时候表其他动作
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: this.myroot + "getProductPage",
				method: "POST",
				data: {
					currentPage: currentPage,
					pageSize: pageSize
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					var data = res.data.data
					//console.log(">>>>>>>>>>>");
					//console.log(data);
					for (var i = 0; i < data.length; i++) {
						var product = {
							id: data[i].id,
							productName: data[i].productName,
							productPrice: data[i].productPrice,
							sales: data[i].sales,
							image: this.myroot + "/upload/" + data[i].image
						}
						//如果是做分页查询既要保持前面也得内容 又要加载新页的内容
						this.productList.push(product)
					}
					if (flag == 1)
						uni.stopPullDownRefresh()
					if (currentPage >= pageCount) {
						this.loadingData = "已经到底了"
						return;
					}

				},
				fail: (res) => {
					uni.showToast({
						title: "请求失败",
						icon: "none",
						duration: 2000
					})
				}
			})
			setTimeout(() => {
				uni.hideLoading();
			}, 1000)
		}
	},
	onPullDownRefresh: function() {
		currentPage = 1;
		this.productList = []; //相当于做初始化，就是要显示第一页的内容就够了，所以要清空列表
		if (currentPage >= pageCount) {
			this.loadingData = "已经到底了"
			return;
		} else {
			this.loadingData = "加载更多"
		}
		this.getProductList(1)
	},
	//computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {



		//异步操作可能导致数据不同同步
		uni.request({
			url: this.myroot + "getListCount",
			method: "POST",
			success: (res) => {
				if (res.data.code == 0) {
					totalItem = res.data.result;
				//	console.log("总记录数>>>>>" + totalItem);
					pageCount = Math.ceil(totalItem / pageSize); //根据总记录数和每页的最大记录数算出总页数
				//	console.log("总页数>>>>>" + pageCount);
					this.getProductList(0); //异步变同步
				}
			}
		})
	},
	onReachBottom() { //只有数据占满屏幕出现滚动条时调用才有效
		this.more(0)

	}



